.dashboard-layout {
  min-height: 100vh;

  .menu-logo-vertical {
    height: 60px;
    margin: 20px;
    background-color: white;
    background-image: url('../media/menu-logo-vertical.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }

  .menu-logo-horizontal {
    height: 60px;
    margin: 10px;
    background-color: white;
    background-image: url('../media/menu-logo-horizontal.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: contain;
  }

  header {
    padding: 0;

    .ant-space {
      display: flex;
      justify-content: space-between;
      height: 64px;
    }
  }

  main {
    margin: 24px 16px;
    padding: 24px;
    min-height: 280px;
  }

  footer {
    text-align: center;
  }

  .collapsed {
    font-size: 16px;
    min-width: 40px;
    height: 40px;
    margin-left: 10px;
  }

  .cart {
    margin-bottom: -10px;
  }
}
