@import "mixins";

#export-pdf {
  font-family: $base__font-family;
  margin-top: 25px;
  .invoice {
    margin: auto;
    position: relative;
    border: 1px solid #cecece;
    padding: 70px 10px 50px 10px;
    width: 794px;
    height: 1140px;
    background-color: white;
    background-repeat: no-repeat, no-repeat;
    background-position: top center, bottom center;
    background-size: contain, contain;
    .content {
      width: 774px;
      height: 1020px;
      overflow: hidden;
      padding-right: 20px;
      padding-left: 20px;
      .ant-descriptions {
        font-family: $base__font-family;
        .ant-descriptions-header {
          margin-bottom: 10px;
          .ant-descriptions-title {
            font-size: 13px;
            padding-right: 15px;
          }
        }
        .ant-descriptions-view {
          .ant-descriptions-row {
            .ant-descriptions-item-label {
              font-size: 12px;
              font-weight: bold;
            }
            .ant-descriptions-item-content {
              font-size: 12px;
            }
          }
        }
      }
      .ant-table {
        font-family: $base__font-family;
        margin-top: 15px;
      }
    }
    .page-number {
      width: 100px;
      height: 40px;
      font-size: 14px;
      text-align: center;
      color: #323131;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}