@import "~antd/dist/reset.css";
@import "~react-quill/dist/quill.snow.css";

@import "mixins";
@import "pending-request";
@import "dashboard.layout";
@import "export-pdf.common";
@import "username.page";
@import "profile.page";
@import "sign-in.page";
@import "sign-up.page";
@import "reset-password.page";

body {
  font-family: $base__font-family;
}

.ant-modal-content {
  .ant-modal-confirm-body,
  .ant-modal-confirm-btns>button {
    font-family: $base__font-family;
  }
}

.ant-select-dropdown {
  .ant-select-item {
    line-height: 2;
  }
}

.ant-message {
  z-index: 999999;
  .ant-message-notice-content {
    font-family: $base__font-family;
  }
}

.reset-coordinates {
  background-color: $white;
  width: 34px;
  height: 34px;
  border-radius: 4px;
  border: 2px solid rgba(0,0,0,0.2);
  background-clip: padding-box;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

header {
  .ant-btn {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.ant-drawer {
  .ant-drawer-body {
    padding: 0;
  }
}

.ant-table {
  thead, tfoot {
    .ant-table-column-title, .ant-table-cell {
      white-space: nowrap;
    }
  }
  .ant-table-tbody {
    tr {
      td {
        white-space: nowrap
      }
      td.break-spaces {
        white-space: break-spaces;
      }
      td.text-align-left {
        text-align: left;
      }
    }
  }
}

.popconfirm-upload {
  .ant-popconfirm-buttons {
    display: none;
  }
}

#sign-in, #sign-up, #reset-password, #profile, #username {
  .ant-input {
    line-height: 2;
  }
  .ant-btn {
    height: 38px;
  }
  .ant-input-prefix {
    margin-inline-end: 10px;
  }
}

.leaflet-control-attribution,  {
  visibility: hidden;
}

.leaflet-container > div:last-child {
  display: none;
}

.captcha {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 34px;
  .reload-captcha {
    font-size: 26px;
    display: flex;
    cursor: pointer;
  }
  .svg-captcha {
    display: flex;
    padding-bottom: 4px;
  }
}

.quill {
  .ql-toolbar {
    direction: ltr;
  }
  .ql-editor {
    text-align: right;
    font-family: $base__font-family;
    line-height: 2;
    ol, ul {
      padding-left: unset;
      padding-right: 1.5em;
      li::before {
        margin-right: -1.5em;
        margin-left: 0.3em;
        text-align: left;
      }
    }
  }
}